.header {
  background-image: url('../images/header-bg-mobile.png');
  background-size: cover;
  background-position-x: 87%;
  height: 100vh;
  width: 100%;

  @include on-tablet {
    background-position: center;
  }

  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    text-align: center;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include on-tablet {
      font-size: 64px;
    }
  }

  &__bottom {
    padding-bottom: 88px;
  }

}
