.nav {
  &__link {
    text-decoration: none;
    color: $menu-text-color;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &:hover {
      padding-bottom: 4px;
      border-bottom: solid 1px $menu-text-color;
      transition-duration: 0.3s;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}
